	left: 0;
	top: 0;
	bottom: 0;
	background-color: #FF324D;
	z-index: -1;
	transition: all 0.3s ease-in-out;
	width: 0;
}

.btn-border-fill::after {
	right: 0;
	left: auto;
}

.btn-border-fill:hover:before,
.btn-border-fill:hover:after {
	width: 50%;
}

.btn-border-fill:hover {
	color: #fff !important;
}

